header {
  position: fixed;
  display: flex;
  top: 0;
  left: 0;
  width: 100%;
  justify-content: space-evenly;
  align-items: left;
  flex-shrink: 0;
  z-index: 999;
  mix-blend-mode: difference;
}

.Logo {
  left: 5%;
  top: 25px;
  position: relative;
  mix-blend-mode: difference;
}

.menu {
  display: none;
}

ul {
  left: 60px;
  display: flex;
  position: relative;
  list-style-type: none;
}
ul a {
  transition: 0.5s;
  text-decoration: none;
  text-align: center;
  left: -80px;
  top: 25px;
  margin: 30px;
  position: relative;
  color: rgb(156, 156, 156);
  font-size: 15px;
  font-family: Poppins;
  text-transform: uppercase;
  word-wrap: break-word;
}

nav ul li a:hover,
nav ul li a.active {
  font-size: 18px;
  color: #D1D1D1;
  text-decoration-line: underline;
}

@media only screen and (max-width: 1024px) {
  header {
    position: fixed;
    display: flex;
    top: 0.5rem;
    left: 0rem;
    width: 100%;
    padding: 0px 25px;
    justify-content: space-evenly;
    align-items: center;
    flex-shrink: 0;
    z-index: 100;
    mix-blend-mode: difference;
  }
  .Logo {
    left: 15px;
    top: 15px;
    position: relative;
  }
  .menu.active {
    opacity: 50%;
  }
  .menu {
    display: flex;
    transition: 0.8s;
    opacity: 100%;
    position: fixed;
    right: 45px;
    top: 25px;
    z-index: 999;
    text-transform: uppercase;
  }
  nav {
    justify-content: space-between;
    position: fixed;
    right: 15px;
    top: 60px;
    height: 40px;
    z-index: 999;
    text-transform: uppercase;
  }
  nav ul {
    left: 70px;
    visibility: hidden;
    opacity: 0;
    transition: 0.7s;
    display: block;
    list-style-type: none;
  }
  nav ul a {
    margin: 0;
    transition: 0.5s;
    border-radius: 0.4em;
    display: block;
    text-decoration: none;
    color: white;
    background: #1f1f1f;
    padding: 0.8em 0.6em;
    font-family: Helvetica;
    font-style: normal;
  }
  ul.active {
    opacity: 1;
    visibility: visible;
  }
  nav ul li a:hover,
  nav ul li a.active {
    color: white;
    background: rgb(59, 59, 59);
    font-size: 12px;
    text-decoration-line: none;
  }
}
