@media only screen and (max-width: 1920px) {

    .WorkH {
        position: absolute;
        left: 0;
        right: 0;
        top:0;
        margin: 50px;
        font-size: 1.5rem;
        mix-blend-mode: difference ;
        font-weight: 50;
    }
    .WorkP {
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        margin: 50px;
        font-size: .8rem;
        mix-blend-mode: difference ;
        text-align: center ;
        
    }
    .outer {
        z-index: 1;
        width: 100%;
        height: 50%;
        position: absolute;
        transform-origin: 0 0; 
        left: 0;
        top: 25%;
    }
    .outer::-webkit-scrollbar{
        display: none;
    }
    .cont {
        position: relative;
        width: 100%; 
        height: 100%; 

       
    }
    
    .outer .inner {
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        overflow-x: auto;
        display: flex;
        scrollbar-width: none;
        
    
    }
    
    .outer .inner div.boxproject {
        position: relative; 

        place-items: center;
     
      
        transition: 1.2s;
        &:hover {
            background-color: rgb(255, 255, 255);
        }
        margin: 0 1px 0 0;
        background-color: rgb(0, 0, 0);
        border-radius: 16px;
        width: 600px;
        height: 100%;
        
    }
    



    .border_2_1 {
        
        transition: 1.2s;
        &:hover {
            background-color: rgb(255, 255, 255);
        }
        margin: 0 1px 0 0;
        background-color: rgb(0, 0, 0);
        border-radius: 16px;
        width: 10%;
        height: 100%;
        flex: 0 0 auto;
    }
    .border_2_2 {
        
        transition: 1.2s;
        &:hover {
            background-color: rgb(255, 255, 255);
        }
        margin: 0 1px 0 0;
        background-color: rgb(0, 0, 0);
        border-radius: 16px;
        width: 10%;
        height: 100%;
        flex: 0 0 auto;
        
    }

    #work_1 {
        width: 9.9%; 
        height: 24.9%; 
        left: 90%; 
        top: 0; 
        position: absolute; 
        transform: 0; 
        transform-origin: 0 0;
        border-radius: 16px;
        
    }

    #work_2 {
        width: 9.9%; 
        height: 24.9%; 
        left: 0; 
        top: 75.1%; 
        position: absolute; 
        transform: 0; 
        transform-origin: 0 0;
        border-radius: 16px;
  
      
    }

    #work_3 {
        width: 89.9%; 
        height: 24.9%; 
        left: 0; 
        top: 0; 
        position: absolute; 
        transform: 0; 
        transform-origin: 0 0;
        border-radius: 16px;
       
    }

    #work_4 {
        width: 89.9%; 
        height: 24.9%; 
        left: 10%; 
        top: 75.1%; 
        position: absolute; 
        transform: 0; 
        transform-origin: 0 0;
        border-radius: 16px;
      
    }


}
